html, body {
  font: {
    family: $primary-font;
    size: 16px; }
  background: {
    color: $bg; } }

a {
  color: $links;

  &:hover {
    color: transparentize($links, 0.2); }

  &:hover, &:active, &:visited, &:link {
    text-decoration: none; }

  &:visited {
    color: $links; } }

hr {
  border: 0px;
  height: 1px;
  background: transparentize($hr-color, 0.3);
  width: 50%;
  margin: 50px auto; }

textarea {
  margin: 15px 0; }

.trumbowyg-editor {
  font: {
    family: $primary-font;
    size: 20px;
    weight: 300; }
  color: $post-paragraph; }

.trumbowyg-button-pane {
  background: #ececec; }

.row {
  margin-left: 0;
  margin-right: 0; }

.read-more {
  color: $featured-text;
  background: transparentize($read-more, 0.2);
  text-transform: uppercase;
  text-decoration: none;
  text-align: center;
  font: {
    family: $primary-font;
    size: 24px;
    weight: 300; }
  border-radius: 0px;
  padding: 10px 30px;
  @include transition(0.2s ease-in-out);

  &:hover {
    background: $read-more;
    color: $featured-text;
    @include transition(0.2s ease-in-out); }

  &:hover, &:active, &:visited, &:link, &:focus {
    text-decoration: none;
    color: $featured-text; }

  &:visited {
    color: $featured-text; } }

.form-margin-top {
  margin: 15px auto 0px; }

.beauty-bar {
  h2 {
    color: $featured-text !important;
    margin: 20px !important;
    border-radius: 5px; }

  .img-wrap {
    height: 100px !important;

    img {
      min-height: 0;
      min-width: 100%; } }

  .featured-post-wrap {
    bottom: 0 !important;
    @include display(flex);
    @include align-items(center);
    @include justify-content(center);
    @include animation(none !important); } }

.pointer {
  cursor: pointer; }

.swal2-modal {
  font: {
    family: $primary-font; } }
